  transition: all 2s;
  position: absolute;
  inset: 0;
}

a {
  color: #dd356e;
  -o-transition: .5s;
  transition: all .5s;
}

a:hover, a:active, a:focus {
  color: #dd356e;
  outline: none;
  text-decoration: none;
}

